@import "font-awesome/font-awesome";
@import "mixin";
@import "color";

body,html {position:relative;width:100%;padding: 0;margin: 0;-webkit-font-smoothing: antialiased;@include font_family();}
input,select,textarea,button {outline: none;@include font_family();}
select{appearance:none;-moz-appearance:none;-webkit-appearance:none;background: url("../img/arrow.png") no-repeat scroll right center transparent;padding-right:20px;}
select::-ms-expand{display:none;}
h1,h2,h3,h4 {margin: 20px 0;-webkit-font-smoothing: antialiased;font-weight: 300;}
h1 {font-weight: 800;font-size:25px;}
ul{list-style:none;padding:0;margin:0;}
a{text-decoration:none;outline:none;color:$black;}
//layout
.container {width:100%;padding:0;margin:0 auto;position:relative;
	//header
	.bread_crumbs{padding-top:61px;}
	.header{width:100%;padding:0;margin:0 auto;position:fixed;background-color:$white;z-index:99999999;box-shadow:0 0 10px $gray_dark;
		&.index_top{position:relative;box-shadow:none;z-index:999;
			.top{z-index:99999999;;position:fixed;}
			.head_cont{padding-top:50px;}
		}
		.top{width:100%;margin:0 0 10px 0;line-height:35px;height:35px;background:$blue;@extend .clearfix;color:$white;
			.top_cont{width:1200px;margin:0 auto;@extend .clearfix;height:35px;min-height:35px;
				.left_nav{float:left;}
				.right_nav{float:right;position:relative;
					.avatar{float:left;margin-top:5px;}
				}
				.left_nav,.right_nav{
					a{color:$white;font-size:14px;padding:0 10px;font-weight:300;
						&:hover,&.active{font-weight:500;}
					}
				}
			}
		}
		.head_cont{width:1200px;margin:0 auto 10px;@extend .clearfix;position:relative;
			.logo{width:136.5px;height:60px;background:url(../img/logo_new.png) no-repeat center;background-size:cover;float:left;}
			.org_logo{float:left;height:80px;margin-left:10px;padding-left:5px;border-left:1px solid $black;
				img{height:100%;}
			}
			.slogan{display:inline-block;margin-top:20px;float:left;}
			.menu_bar{height:50px;float:right;width:auto;text-align:right;
				ul{height:25px;list-style:none;
					li{float:left;padding:0 10px;line-height:25px;text-align:center;font-weight:300;
						a{text-decoration: none;color:$dark;font-size:14px;padding-right:3px;}
						i{color:$gray;}

						&:hover, &.active{
							a{transition: all 0.3s ease-in-out;color:$blue_sky;font-weight:500;}
						}
					}
					&.org_menu{clear:both;}
				}
				.search_form{margin:5px 0 0;height:26px;width:200px;display:inline-block;border:1px solid $gray_dark;border-radius:14px;transition: all .3s ease-out;
					input[type=text]{display:inline-block;height:26px;padding:0 10px;line-height:30px;box-sizing:border-box;border:0 none;background-color:transparent;width:calc(100% - 30px);float:left;font-size:14px;}
					button{height:26px;width:26px;line-height:26px;text-align:center;float:left;background-color:transparent;border:0 none;
						i{color:$gray_dark;font-size:18px;}
					}
					&:hover{border-color:$blue_sky;width:300px;transition: all .3s ease-in;
						button{
							i{color:$blue_sky;}
						}
					}
				}
				&.org_menu{position:absolute;float:none;right:0;width:100%;
					ul{position:absolute;bottom:-38px;right:0;
						li{
							&:hover,&.active{border-bottom:2px solid $blue_sky;
								a{color:$black;}
							}
							&:hover{border-bottom:2px solid $gray_dark;}
						}
					}
				}
			}
		}
		&.header_main{border-bottom:1px solid $gray_darker;
			.head_cont{margin:10px auto;
				.logo{width:91px;height:40px;}
				.menu_bar{height:40px;
					ul{float:left;clear:none;
						li{line-height:40px;}
					}
				}
				.search_form{float:left;margin:5px 0 0 20px;}
				.right_nav{float:left;margin:8px 0 0 20px;font-size:14px;
					a{color:$gray_darker;}
					.avatar{float:left;margin:0 5px 0 0;}
				}
			}
		}
		.right_nav{position:relative;
			.user_menu_list{display:none;position:absolute !important;width:140px !important;padding:10px 0;background-color:$white !important;clear:both !important;z-index:9999999999999 !important;box-shadow:0 3px 3px $gray_darker;float:none !important;height:310px !important;border:1px solid $gray;
				li{clear:both;display:block;float:none !important;background-color:$white;padding:0 20px;text-align:center !important;height:35px !important;line-height: 35px !important;
					a{color:$black !important;}
					&:hover{background-color: $gray;}
					&.top_border{border-top:1px solid $gray;}
				}
			}
			&:hover{
				.user_menu_list{display:block;transition:all .3s ease-in-out}
			}
		}
	}
	.slider{width:100%;height:400px;margin:0 0 30px;padding:0;overflow:hidden;background-color:$gray_light;text-align:center;
		.bxslider{width:100%;height:400px;overflow:hidden;
			li{height:400px;animation:panoramic2 20s linear infinite alternate;}
		}
	}
	.bread_crumbs{width:1200px;margin:0 auto;line-height:30px;font-size:12px;height:30px;border:{bottom:1px solid $gray;};
		li{line-height:35px;margin:0 5px;float:left;
			a{color:$gray_darker;}
			i{color:$gray_darker;}
			&:first-child{margin:0 5px 0 0;}
		}
	}
	.filter_cont{margin:20px auto;width:1200px;@extend .clearfix;
		ul{width:100%;height:30px;padding:0;@extend .clearfix;
			li{float:left;line-height:20px;font-size:14px;height:20px;overflow:hidden;
				&:first-child{text-align:left;font-weight:bold;width:4em;}
				&:last-child{width:calc(100% - 4em);}
				span{float:left;margin-right:20px;padding:0 5px;display:table-cell;line-height:20px;cursor:pointer;
					&.explore{display:none;float:right;margin-right:0;}
					&.active{background-color:$blue_sky;color:$white;}
					&.disabled{color:#999;}
				}
				&.so_many{
					span{
						&.explore{display:inline-block;}
					}
				}
			}
			&.no_hidden{height:auto;
				li{overflow:auto;height:auto;margin-bottom:10px;
					span{margin-bottom:10px;}
				}
			}
		}
	}
	.item_list,.project_info{
		li{border-bottom:1px solid $gray;padding:20px 0;font-size:14px;@extend .clearfix;
			.item_cover{width:240px;height:160px;overflow:hidden;float:left;margin-right:20px;display:inline-block;position:relative;
				i{color:rgba($white,.5);font-size:50px;top:50%;left:50%;margin:-25px 0 0 -25px;position:absolute;}
				.ribbon{position:absolute;display:inline-block;top:0;right:0;padding:5px 10px;font-size:14px;font-weight:bold;color:$white;
					&.live{background-color:$red;}
					&.soon{background-color:$blue_sky;}
					&.stop{background-color:$gray_dark;}
				}
			}
			.item_cont{width:calc(100% - 260px);float:left;
				.title{font-size:16px;font-weight:600;margin:0;
					span{float:right;color:$red;font-size:14px;font-family:"hiragino sans GB",Arial,Verdana,sans-serif;font-weight:bold;}
				}
				.info{margin:10px 0;border:0 none;
					span{width:33%;}
				}
				.foot{margin:10px 0;
					span{width:50%;}
				}
				.info,.foot{@extend .clearfix;
					span{line-height:25px;border:0 none;padding:0;margin:0;float:left;display:inline-block;color:$gray_darker;font-size:14px;}
				}
				.desc{height:44px;line-height:22px;overflow:hidden;clear:both;padding:0;margin:0;}
			}
		}
	}
	.project_info{margin:0 auto;width:1200px;
		li{border:0 none;
			.p_title{font-size:20px;font-weight:600;margin:0 0 20px;
				.right_icon{float:right;
					i{color:$blue_sky;margin-right:30px;font-size:30px;cursor:pointer;
						&.fa-heart{color:$red;}
					}
				}
			}
			.p_sub_title{font-size:14px;margin:0;}
			.item_cover{width:400px;height:283px;}
			.item_cont{width:calc(100% - 420px);margin:0 0 20px;
				.title{font-size:14px;font-weight:normal;
					.icon{color:$gray_dark;}
					span{color:$blue_sky;}
				}
				.info{margin-bottom:0;
					span{line-height:40px;}
				}
				.desc{height:88px;line-height:22px;margin-bottom:20px;}
				.big_num{margin:20px 0 40px;}
				.foot{margin-top:0;
					span{line-height:40px;
						&:first-child{width:66%;}
						&:last-child{width:33%;}
					}
				}
			}
		}
		&.teacher_info{
			.item_cover{width:200px;height:200px;}
			.item_cont{width:calc(100% - 220px);}
		}
	}
	.card_list{width:100%;margin:30px auto;@extend .clearfix;
		li{width:calc((100% - 60px) / 4);margin:0 20px 20px 0;border:1px solid $gray;border-radius:10px;overflow:hidden;float:left;box-sizing:border-box;
			&:nth-child(4n){margin:0 0 20px;}
			.top_cont{width:100%;padding:20px;background-color:darken($gray_light,5%);box-sizing:border-box;
				h2{line-height:20px;padding:0 20px;border-left:4px solid $blue_sky;font-size:18px;font-weight:bold;margin:0;}
			}
			.user_cont{padding:20px;min-height:160px;text-align:center;position:relative;
				.title{position:absolute;left:10px;top:10px;color:$orange;font-weight:bold;font-size:14px;}
				h2{font-size:18px;}
				h3{font-size:14px;}
			}
			&:hover{border-color:$blue_sky;box-shadow:0 0 5px rgba($black,.5);
				.top_cont{background-color:$blue_sky;
					h2{color:$white;border-color:$white;}
				}
				.user_cont{
					.avatar{box-shadow:inset 0 0 5px $black !important;}
				}
			}
		}
	}
	.name_list{padding:0 20px;box-sizing:border-box;@extend .clearfix;
		li{float:left;width:33%;
			span{margin-left:30px;font-weight:normal;color:$gray_darker;}
		}
		&.dot_name{
			li{font-weight:bold;
				i{color:$blue_sky;font-weight:normal;margin-right:5px;}
			}
		}
	}
	.course_video{width:calc(100% - 400px);float:left;margin:0;position:relative;height:100vh;overflow:hidden;
		.logo{width:91px;height:40px;background:url(../img/logo_new.png) no-repeat center;background-size:cover;position:absolute;right:20px;top:20px;z-index:999999;}
		.go_back{height:30px;padding:0 15px;line-height:30px;border-radius: 6px;background:$blue_sky;color:$white;font-weight:bold;font-size:16px;position:absolute;left:20px;top:20px;z-index:999999;cursor:pointer;}
		.course_title{position:absolute;top:20px;left:0;right:0;margin:auto;width:70%;font-size:16px;text-align:center;
			h2,h3{margin:0;font-size:18px;}
			h3 span{float:left;width:33%;display:inline-block;font-size:14px;}
		}
		iframe{position:absolute;height:calc(100vh - 100px);bottom:0;}
	}
	.course_right{width:360px;float:left;height:calc(100vh - 40px);background-color:$gray;padding:20px;overflow-x:hidden;
		h2,h3{margin:0 0 5px;font-size:14px;}
		h2{font-weight:bold;}
		h3{font-size:12px;}
		img{margin-left:10px;width:120px;}
		.course_list{
			li{font-size:16px;font-weight:bold;
				a{color:$blue;text-decoration:none;}
			}
			ul{margin:10px;
				li{font-size:14px;font-weight:normal;line-height:25px;
					i{margin:0 10px;
						&.fa-check-circle{color:$green;}
						&.fa-circle{color:$blue-sky;}
						&.fa-circle-o{color:$gray_dark;}
						&.fa-info-circle{color:$blue_sky;display:none;font-size:12px;}
						&.fa-info{color:$gray_darker;display:none;font-size:12px;}
					}
					span{float:right;color:$gray;}
				}
			}
		}
		.comments_list{margin:30px 0 0;padding-bottom:30px;height:calc(100vh - 470px);overflow-x:hidden;overflow-y:auto;
			li{padding:10px 0;@extend .clearfix;font-size:14px;border-bottom:1px solid $gray_light;
				p{padding:0;margin:0;}
				.avatar{float:left;margin:0 10px 0  0;}
				.name{font-weight:bold;}
				.date{float:right;color:$gray_dark;}
				.cont{margin-top:10px;color:$black;}
			}
		}
		.comment_form{margin:20px 0;
			label{display:block;margin-bottom:10px;font-size:14px;font-weight:bold;}
			textarea{border:1px solid $gray;font-size:14px;padding:10px;border-radius:6px;resize:none;min-height:8em;width:100%;box-sizing:border-box;box-shadow:0 0 10px $gray inset;}
			.foot{font-size:14px;
				.btn{margin-right:0;float:right;}
				.text_count{
					.num_count{color:$red;font-weight:bold;font-family:Georgia,sans-serif;margin:0 5px;font-size:18px;}
				}
			}
		}
	}
	.ability_cont{position:relative;width:80%;margin:30px auto;min-height:600px;
		span{position:absolute;border-radius:100%;display:inline-block;text-align:center;cursor:pointer;
			&.sub{width:100px;min-height:100px;z-index:999;border:2px dashed $gray_dark;text-align:center;background-color:$white;left:50%;top:50%;right:auto;bottom:auto;margin:-62.5px 0 0 -50px;
				span{display:table;box-sizing:border-box;width:100%;height:100%;text-align:center;margin:0 auto;
					em{font-style:normal;font-size:14px;text-align:center;line-height:20px;color:$gray_darker;display:table-cell;padding:0 10px;height:100px;vertical-align:middle;box-sizing:border-box;
						i{color:$white;display:none;}
					}
				}
				&.active{background-color:$red;border: 0 none;
					em{color:$white;}
					&:hover{transition:all .3s ease;transform: scale(1.5,1.5);
						em{width:100px;transform: scale(1,1);text-indent:-10px;
							i{display:block;font-size:25px;transform: scale(1,1);margin-left:10px;}
						}
					}
				}
			}
		}
		.main{width:300px;height:60px;top:50%;left:50%;margin:-150px 0 0 -150px;font-weight:bold;text-align:center;font-size:30px;line-height:30px;padding:120px 0;background-color:$white;box-shadow: 0 0 20px $gray;z-index:9999;}
		div.bg_circle{position:absolute;width:500px;height:500px;left:50%;top:50%;margin:-250px 0 0 -250px;border:10px solid $gray_dark;z-index:99;box-sizing:border-box;border-radius:100%;}
	}
	.ability_main{width:100%;min-height:880px;position:relative;overflow:hidden;
		div,span{position:absolute;text-align:center;vertical-align:middle;cursor:pointer;z-index:9999;}
		div{
			ul{position:absolute;top:-60px;height:30px;display:none;min-height:300px;padding:30px 20px 50px 200px;box-sizing:border-box;z-index:99;
				li{border:1px solid $gray_dark;line-height:25px;margin:0 0 10px 98px;text-align:left;color:$gray-darker;padding:0 10px;white-space:nowrap;
					&:first-child{left:5px;position:absolute;top:110px;width:145px;border:{top:1px solid red;right:0;bottom:0;left:0;};height:1px;
						span{width:5px;height:5px;border-radius:100%;background-color:red;margin:-3px 0 0 -10px;}
					}
					&:last-child{
						span{border:{top:1px solid red;left:0;}}
					}
					&:not(:first-child):hover{border:1px solid red;color:$black;font-weight:600;}
					span{line-height:36px;height:36px;margin:12px 0 0 -41px;width:30px;border:{top:1px solid red;right:0;bottom:0;left:1px solid red;};}
				}
			}
			&.no_cont:hover{
				ul{display:none;}
			}
		}
		.comment_item{right:0;top:50px;height:calc(100% - 50px);width:300px;display:none;transition:all .5s ease;
			span{width:100px;height:100px;position:relative;left:0;right:0;margin:auto auto 40px;font-size:25px;line-height:25px;padding:25px 0;color:$blue;font-weight:600;background-color:$white;display:block;box-shadow:0 0 20px $gray;border-radius:100%;box-sizing:border-box;}
			.item_list{background-color:$gray_dark;color:$white;padding:10px 20px;margin-bottom:20px;text-align:center;min-height:35px;position:relative;
				&:hover{background-color:$blue_sky;}
			}
		}
		.big_circle{width:240px;padding:150px 0 50px 260px;height:300px;line-height:100px;background-color:$white;box-shadow:0 0 50px $gray_dark;left:-250px;top:50%;margin-top:-250px;border-radius:250px;font-size:50px;font-weight:bold;color:red;}
		.big_circle_line{width:700px;height:700px;border:20px solid $gray;border-radius:100%;left:-350px;top:50%;margin-top:-350px;box-sizing:border-box;box-shadow:0 0 10px $gray_dark;}
		.circle_item{width:100px;height:100px;border-radius:50px;color:$white;background-color:$red;line-height:100px;box-shadow: 0 0 10px $gray_dark;
			&.circle_1{left:20px;top:50px;}
			&.circle_2{left:140px;top:100px;}
			&.circle_3{left:230px;top:200px;line-height:20px;padding-top:20px;height:80px;}
			&.circle_4{left:280px;top:340px;line-height:20px;padding-top:20px;height:80px;}
			&.circle_5{left:280px;top:480px;}
			&.circle_6{left:220px;top:600px;}
			&.circle_7{left:120px;top:680px;}
			&.circle_8{left:10px;top:720px;}
			&.no_cont{border:2px dashed $gray_dark;background-color:$white;color:$gray_darker;}
		}
	}
	//content
	.content{width:1200px;margin:0 auto;padding:50px 0;position:relative;@extend .clearfix;
		.img_list,.item_list li{float:left;width:calc((1200px - 60px) / 4);margin-right:20px;padding:0;min-height:200px;border:1px solid $gray;box-sizing:border-box;color:$gray_darker;position:relative;overflow:hidden;
			&:last-child{margin-right:0;}
			.cover{height:200px;overflow:hidden;position:absolute;top:0;left:0;z-index:10;}

			.item_cover{position:relative;height:200px;width:100%;overflow:hidden;top:0;left:0;z-index:10;float:none;margin-right:0;
				.item_title{position:absolute;display:table;bottom:0;width:100%;height:25px;background-color:rgba($black,.7);
					span{margin-right:10px;color:$white;display:table-cell;text-align:center;
						&.ribbon{position:relative;bottom:auto;top:auto;padding:5px 0;
							&.soon{background-color:$green;}
							&.playback{background-color:$blue_sky;}
						}
					}
				}
			}
			.item_cont{float:none;width:100%;padding:10px 10px 0;box-sizing:border-box;
				.title{font-size:14px;width:100%;height:25px;white-space:nowrap;line-height:25px;overflow:hidden;text-overflow: ellipsis;}
				.info{line-height:25px;
					span{width:50%;
						&:last-child{text-align:right;}
					}
				}
				.foot{border-top:1px solid $gray;width:calc(100% + 20px);margin:0 0 0  -10px;padding:5px 0;line-height:25px;text-align:center;
					span{width:100%;}
				}
			}
			img{height:100%;min-width:100%;}
			.wrap{padding:10px;position:absolute;bottom:0;top:60%;left:0;width:calc(100% - 20px);background-color:rgba($white, .8);z-index:11;transition: all .3s ease;
				.title{font-size:16px;color:$black;font-weight:500;margin:10px 0;}
				.sub_title{font-size:14px;margin:10px 0;
					.money{font-weight:600;color:$red;}
				}
				.desc{font-size:14px;display:none;color:$black;}
				a{outline:none;color:$gray_darker;text-decoration:none;}
				&:hover{top:0;bottom:0;height:100%;transition: all .3s ease;
					.desc{display:block;}
				}
			}
			&:hover{cursor:pointer;border:1px solid $gray_dark;box-shadow:0 0 10px $gray_darker;transition: all .3s ease-in-out;}
		}
		.item_list{min-height:200px;
			li{margin:0 20px 20px 0;float:left;clear:none;
				&:nth-child(4n){margin:0 0 20px;}
			}
			&.teacher_course{min-height:230px;
				li{min-height:230px;
					.item_title{color:$white;text-align:center;line-height:30px;}
					.item_cont{padding:0;margin:-7px;
						.foot{width:100%;margin:0;border:0 none;}
					}
				}
			}
			&.item_img_list{
				li{height:290px;position:relative;color:$black;
					.item_cover{height:170px;margin-bottom:0;position:absolute;top:0;z-index:100;}
					.item_cont{padding:10px;position:absolute;bottom:0;z-index:101;background-color:rgba($white,.7);color:$black;
						.title{border:0 none;}
						.info,.desc{display:none;}
						.info{
							span:last-child{text-align:left;width:100%;font-size:14px;color:$black;}
						}

						.foot{padding:10px 0;
							span{display:inline-block;width:auto;color:$black;font-size:14px;
								&:first-child{float:left;margin-left:10px;font-weight:bold;}
								&:last-child{float:right;margin-right:10px;}
							}
						}
					}
					&:hover{
						.item_cont{
							.info,.desc{display:block;}
						}
					}
				}
			}
		}
		.main_title{border-left:3px solid $blue_sky;margin:0 0 20px;line-height:30px;font-size:20px;font-weight:500;padding:0 0 0 20px;color:$black;
