			a{color:$white;margin-right:20px;font-size:14px;text-align:center;display:inline-block;
				span{display:block;font-size:18px;font-weight:bold;margin-bottom:5px;}
			}
		}
		.user_big_avatar{border:5px solid $blue_sky;width:150px;height:150px;border-radius:100%;overflow:hidden;margin:60px 20px 0 0;float:left;
			img{width:100%;box-shadow:0 0 20px $black inset;}
		}
		.user_info_list{float:left;color:$white;margin:80px 0 0;
			h2{font-size:20px;font-weight:bold;margin:0 0 20px;}
			h3{font-size:14px;}
		}
	}
	//footer
	.footer{position:relative;width:100%;margin:0;@extend .clearfix;border-top:1px solid $gray;}
	.foot-top{position:relative;width:auto;margin:10px auto;@extend .clearfix;
		ul{padding:0;list-style:none;margin:0 auto;display:table;
			li{display:inline-block;float:left;width:130px;font-size:14px;color:$white;
				a{display:block;font-size:14px;color:$black;line-height:25px;text-decoration:none;
					&:hover{color:$blue;}
				}
			}
		}
	}
	.foot_bottom{width:1200px;line-height:35px;height:35px;padding:0 0 20px;margin:0 auto;font-size:12px;text-align:center;display:table;vertical-align:middle;
		.logo{display:table-cell;width:360px;height:30px;background:url(../img/logo_footer.png) no-repeat right;background-size:auto 100%;margin-right:30px;}
		.share{display:table-cell;height:30px;line-height:30px;font-size:14px;width:300px;text-align:left;padding-left:30px;
			a{display:inline-block;color:$black;cursor:pointer;text-decoration:none;
				&:hover{color:$white;}
			}
		}
	}
}
.filter_gray{filter:grayscale(100%);
	&:hover{filter:none;-webkit-filter: grayscale(0%);}
}
.icon {width: 1em; height: 1em;vertical-align: -0.15em;fill: currentColor;overflow: hidden;}
.paginate{display:table;margin:20px auto;height:30px;border-collapse: collapse;@extend .clearfix;
	li{display:table-cell;text-align:center;padding:0 8px;margin:0 2px;width:1em;height:1em;color:$gray_darker;font-size:14px;vertical-align:middle;cursor:pointer;
		&.prev,&.next,&.more{width:3em;border:0 none;}
		&.first_page,&.last_page{width:2em;border:0 none;}
		&.active{background-color:$blue_sky;color:$white;border-radius:100%;}
	}
}
.info_list{width:100%;margin:20px auto;
	li{line-height:30px;font-size:14px;border-bottom:1px solid $gray;padding:5px 0;@extend .clearfix;
		i{width: 30px;margin-right:10px;color:$gray;font-size:20px;text-align:center;}
		.avatar{float:left;margin:5px 10px 0 10px;}
		.ctrl{float:right;margin-top:30px;display:none;color:$gray_darker;}
		a{color:$blue_sky;}
		p{margin:0;}
		&:hover{
			.ctrl{display:inline-block;transition:all .3s ease-in-out;}
		}
		&.unread{background-color:$gray_light;font-weight:bold;}
	}
}
.avatar{width:100px;height:100px;overflow:hidden;border-radius:100%;display:inline-block;
	&.big{width:150px;height:150px;}
	&.small{width:80px;height:80px;
		a{width:80px;height:80px;font-size:14px;line-height:80px;}
		img{width:80px;}
	}
	&.tiny{width:50px;height:50px;
		a{width:50px;height:50px;color:$red;font-size:12px;text-align:center;line-height:50px;}
		img{width:50px;}
	}
	&.tiny_reply{width:40px;height:40px;
		a{width:40px;height:40px;}
		img{width:40px;}
	}
	&.login{width:25px;height:25px;
		a{width:25px;height:25px;}
		img{width:25px;height:25px;}
	}
	a{text-align:center;display:inline-block;width:100%;height:100%;overflow:hidden;border-radius:100%;box-sizing:border-box;}
	img{width:100%;box-sizing:border-box;}
}
// form
.popup{display:none;width:100%;height:100vh;padding:0;margin:0 auto;overflow:hidden;position:absolute;top:0;left:0;
	.mask{width:100%;height:100vh;background-color:rgba($black,.5);position:fixed;top:0;left:0;}
	.popup_cont{padding:20px;border-radius:10px;background-color:$white;position:fixed;top:50vh;left:50%;z-index:999999;
		h2{font-size:18px;border-bottom:1px solid $blue_sky;margin-top:0;
			span.close{font-size:20px;width:auto;height:30px;color:$gray;float:right;margin:-10px 0 0;}
		}
		&.login_form,&.normal{width:500px;margin:-300px 0 0 -270px;}
		&.cert{width:600px;margin:-400px 0 0 -350px;
			.cert_img{width:500px;}
		}
		.foot{text-align:center;padding:10px 0 0;border-top:1px solid $blue_sky;margin-top:20px;}
	}
}
.join_form{width:500px;margin:30px auto;
	&#popup_form{margin:20px auto;width:80%;}
	.input_div{margin-bottom:10px;display:table;table-layout:fixed;font-size:12px;width:100%;
		label{line-height:25px;margin-bottom:5px;display:table-caption;font-size:16px;font-family: 'Lantinghei SC', 'hiragino sans GB', 'Microsoft YaHei', 'Helvetica Neue', Helvetica, Arial, sans-serif !important;font-weight:600;
			em{font-size:12px;float:right;color:red;font-weight:bold;font-style:normal;}
		}
		input[type="text"],input[type='password'],select,textarea{padding:5px;background:$white;color:$black;box-sizing:border-box;width:100%;border:1px solid $gray_darker;font-size:14px;border-radius:7px;
			&.error{border:1px solid $red;background-color:$gray_light;}
		}
		select{width:auto !important;background: url("../img/arrow.png") no-repeat scroll 95% center transparent;}
		input[name=verify_code]{width:50%;max-width:50%;min-width:50%;margin-right:5px;display:inline-block;}
		button[name=verify_btn]{width:calc((100% / 2) - 5px);display:inline-block;font-size:16px;padding:5px;background-color:$blue_sky;border:0 none;color:$white;}
		input[type="radio"]{display:none;}
		span{display:table-cell;width:calc(90vw / 3);vertical-align:bottom;height:18vh;text-align:center;font-family: 'Lantinghei SC', 'hiragino sans GB', 'Microsoft YaHei', 'Helvetica Neue', Helvetica, Arial, sans-serif !important;
			&.active{background-color:$white !important;box-sizing:border-box;box-shadow:0 0 5px rgba($black, .9) inset;}
		}
		.tip{margin-left:5px;color:$red;line-height:30px;}
	}
	.btn_div{margin-top:20px;
		.btn{box-shadow: 0 0 10px rgba($white, .8);}
	}
	.pop_up{width:80vw;padding:10px;display:none;background:$gray_light;box-shadow:0,0,5px rgba($black, .8);border-radius:6px;position:absolute;top:100px;left:0;right:0;margin:auto;z-index:99999;
		.pop_title{font-size:16px;font-weight:bold;color:red;border-bottom:1px solid $black;padding-bottom:10px;margin-bottom:10px;text-align:center;}
		.pop_cont{font-size:14px;}
	}
	&.inline_form{width:80%;
		label{float:left;width:7em;text-align:right;margin:0 10px 0 0;font-weight:normal;font-size:14px;
			em{float:none;}
		}
		input[type=text],input[type=password],select,textarea{width:auto;min-width:12em;float:left;}
		input[name=verify_code],button[name=verify_btn]{width:8em;max-width:8em;min-width:8em;}
		select{width:auto;box-sizing: content-box;padding:0 20px 0 10px;line-height:30px;height:30px;margin-right:10px;overflow:hidden;}
	}
	&.all_width{width:100% !important;}
}
.btn{border:0 none;padding:5px 10px;display:inline-block;box-sizing:border-box;font-size:16px;font-weight:600;text-align:center;cursor:pointer;text-decoration:none;margin-right:20px;border-radius:6px;
	&.btn_orange{background-color:$orange;color:$white;
		&:hover{background-color:darken($orange, 5%);transition: all 0.3s ease-in-out;}
	}
	&.btn_red{background-color:$red;color:$white;
		&:hover{background-color:darken($red,5%);transition: all 0.3s ease-in-out;}
	}
	&.btn_gray{background-color:$gray_dark;color:$white;
		&:hover{background-color:darken($gray_dark,5%);transition: all 0.3s ease-in-out;}
	}
	&.btn_green{background-color:$green;color:$white;
		&:hover{background-color:darken($green,5%);transition: all 0.3s ease-in-out;}
	}
	&.btn_blue{background-color:$blue_sky;color:$white;
		&:hover{background-color:darken($blue_sky,5%);transition: all 0.3s ease-in-out;}
	}
	&.btn_s{padding:3px 10px;font-size:12px;font-weight:600;margin-right:10px;}
	&.btn_big{padding:8px 15px;font-size:18px;font-weight:600;margin-right:10px;}
	&.btn_block{display:block;}
	&:hover{color:$white;transition: all 0.3s ease-in-out;}
	&.btn_border{border:1px solid $dark;border-radius:17px;background-color:transparent;color:$dark;width:auto;padding:0 30px;height:35px;line-height:35px;
		&:hover{border:1px solid $orange;color:$orange;transition: all 0.3s ease-in-out;}
		&.blue_border{border-color:$blue_sky;border-radius:6px;height:22px;line-height:22px;padding:0 15px;font-size:14px;font-weight:normal;color:$black;
			&:hover{background-color:$blue_sky;color:$white;}
		}
		&.orange_border{border-color:$orange;border-radius:6px;height:22px;line-height:22px;padding:0 15px;font-size:14px;font-weight:normal;color:$black;
			&:hover{background-color:$orange;color:$white;}
		}
	}
}

.tabs{margin-top:20px;
	ul.tab_list{display:table;width:100%;margin-bottom:20px;background-color:$white;padding:3px 0 0;border-bottom:1px solid $gray;
		li.tab{float:left;margin:0 25px 0 0;padding:5px 0;font-size:16px;line-height:30px;cursor:pointer;
			&.active{font-size:16px;font-weight:600;border-bottom:2px solid $blue_sky;line-height:28px;}
		}
	}
	div.tab_cont{min-height:100px;padding-bottom:30px;
		.foot{margin:10px auto;color:$blue_sky;text-align:center;
			a{color:$blue_sky;font-size:14px;}
		}
	}
	&.aside_tabs{
		ul.tab_list{background:transparent;padding:0;border-color:$gray_dark;border-collapse: collapse;table-layout:fixed;
			li.tab{display:table-cell;text-align:center;}
		}
	}
}

.data_report{@extend .clearfix;width:100%;margin-top:30px;
	span{float:left;display:inline-block;text-align:center;
		&.title{width:5em;font-size:20px;font-weight:bold;line-height:100px;margin-right:100px;}
		&.circle{margin:0 100px 0 0;width:100px;height:100px;border:5px solid $gray;border-radius:100%;position:relative;box-sizing:border-box;
			&:last-child{margin:0;}
			.border{position:absolute;right:-5px;top:-5px;
				&.green{background-color:$green;height: 100px;width:50px;border-radius:0 50px 50px 0;}
				&.red{background-color:$red;height: 50px;width:50px;border-radius:0 50px 0 0;}
				&.blue{background-color:$blue;height: 100px;width:50px;border-radius:0 50px 50px 0;}
				&.orange{background-color:$orange;height: 50px;width:50px;border-radius:0 50px 0 0;}
			}
			.cont{background-color:$white;width:90px;height:90px;border-radius:45px;position:absolute;left:0;top:0;padding:25px 0;box-sizing:border-box;font-size:12px;
				span{float:none;font-size:20px;font-weight:bold;display:block;}
			}
		}
	}
}
table{width:100%;border-collapse:collapse;border:{top:2px solid $gray;bottom:2px solid $gray;};margin:20px 0;
	th,td{font-size:12px;padding:5px;text-align:center;}
	th{font-weight:bold;font-size:14px;}
	td.ctrl{white-space: nowrap;}
	tr{border-bottom:1px solid $gray;}
}
.clearfix, .clear {zoom: 1;clear: both;}
.clearfix:before, .clearfix:after, .clear:before, .clear:after {content: " ";display: table;}
.clearfix:after, .clear:after {clear: both;}

.float_right{float:right;}
.float_left{float:left;}
.text_center{text-align:center;}
.text_right{text-align:right !important;}
.font_red{color:red !important;}
.font_blue{color:$blue_sky !important;}
.font_green{color:green !important;}
.font_m{font-size:14px !important;}
.font_s{font-size:12px !important;}
.font_big{font-size:16px !important;}
.font_bold{font-weight:bold !important;}
.big_num{font-size:30px;font-weight:bold;color:$red;}
.no_border{border:0 none !important;padding:0 !important;}
.font_width{text-align:justify !important;width:100px !important;display:inline-block !important;}
.info{font-size:14px;margin:20px;
	i{color:$orange;}
}
@for $i from 1 through 20 {
	.ani_#{$i}s{-webkit-animation-delay:$i*0.3s;animation-delay:$i*0.3s;}
}
@for $i from 1 through 20 {
	.ani_#{$i}d{-webkit-animation-duration:$i*0.5s;animation-duration:$i*0.5s;}
}

@keyframes panoramic{
	to{
		background-position:100% 0;
	}
}
@keyframes panoramic2{
	to{
		background-position:0 100%;
	}
}
@-webkit-keyframes panoramic{
	to{
		background-position:100% 0;
	}
}
@-webkit-keyframes panoramic2{
	to{
		background-position:0 100%;
	}
}
@media print
{
	.no_print, .no_print *
	{
		display: none !important;
	}
}